body {
  background: orchid;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif 
  -webkit-font-smoothing; 
  
}
.App{
  background: darkblue;
  min-height: 500px;
  width: 400px;
  margin: 20px auto;
}


.app-title{
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color:khaki;

}
ul {
  list-style: none;
 
}
AiFillCloseCircle {
  
  position:  left ;
  cursor: pointer;
}

.btn{
  height: 30px;
  width: 30px;
  margin: 0;
 outline:none;
background-color: darkblue;
outline: none;
border: 0;
color: cornsilk;
right: 25px;
}
.AddBtn{
 font-weight: bold;
  width: 80px;
  height: 50px;
  border-radius: 5px;
  background-color:darkorange;
  color:khaki;
cursor: pointer;
border:0;
}
.input{
 
width: 200px;
height: 50px;
border-radius: 10px;
padding: 0 20px;
margin: 20px;
border:0;
color:khaki;
background-color: teal;
}

.listN {
 background-color: crimson;
font-weight: bold;
outline: none;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color:khaki;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
